import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HomeRoots } from './home.routes';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CollapseModule } from '../../../app/component/common/collapse';
import { DrawerResolve } from '../../../app/rtf-common';
@NgModule({
  imports: [CommonModule, CollapseModule.forRoot(), HomeRoots],
  declarations: [DashboardComponent],
  entryComponents: [DashboardComponent],
  providers: [DrawerResolve]
})
export class HomeModule {
  static entry = DashboardComponent;
}



