import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { I18n_frm } from '../../../models/rtf.i18n.model';
import { CacheService } from '../../../../app/rtf-common';
import { i18n_dashboard } from '../../../models/mock.i18n.model';
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  // styleUrls: ['./dashboard.component.css'],
  host: {
    class: 'd-flex flex-column g-main'
  }
})
export class DashboardComponent implements OnInit {
  // 临时用于折叠
  collapse_01 = false;
  collapse_02 = false;
  collapse_03 = false;
  collapse_04 = false;
  collapse_05 = false;
  title: string;
  languageCode = 'zh-CHS';
  i18n: any;
  constructor(private router: Router, private cache: CacheService) { }
  ngOnInit() {
    if (this.cache.exists('languageCode')) {
      this.languageCode = this.cache.get('languageCode');
    }
    this.changeLanguage(this.languageCode);
    this.i18n = i18n_dashboard;
  }

  navigateTo(url: string) {
    this.router.navigateByUrl(url);
  }

  changeLanguage(lanCode: string) {
    this.title = I18n_frm[lanCode]['dashboardTitle'];
  }
  gotoIDE() {
    const ssid = this.cache.get('session');
    const ideUrl = '/platform/dev/main/web/webide/index.html#/ssologin?ssid=' + ssid;
    window.open(ideUrl, '_blank');
  }
}



